"use client";
import Image from "next/image";
import { useState } from "react";

interface ImageGalleryProps {
  images: { id: number; image: string }[]; // تغيير النوع إلى مصفوفة الكائنات
}

export default function ImageGallery({ images }: ImageGalleryProps) {
  const [selected, setSelected] = useState(0);

  return (
    <div className="flex flex-col gap-4 items-center">
      <Image
        src={images[selected].image}
        alt={`صورة الإعلان ${selected + 1}`}
        className="w-full aspect-square object-cover rounded-lg shadow"
      />

      <div className="flex gap-2 overflow-x-auto max-w-full">
        {images.map((img, i) => (
          <button key={img.id} onClick={() => setSelected(i)}>
            <Image
              src={img.image}
              className={`w-20 h-20 object-cover rounded border ${
                selected === i ? "border-cgreen" : "border-gray-200"
              }`}
              alt={`صورة ${i + 1}`}
            />
          </button>
        ))}
      </div>
    </div>
  );
}